<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process275"></a>

<p><br></p>
<p><span class="rvts17">重生的国王（</span><span class="rvts50">A King Reborn</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:109,Y:902</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：塔昆（</span><span class="rvts20">Tarquin</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">告诉塔昆达利斯复活了布雷克斯</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1513.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">在</span><span class="rvts20"></span><span class="rvts21">最后一名魔导师</span><span class="rvts20"></span><span class="rvts21">支线中，打败雷蒙德和他的</span><span class="rvts20">3</span><span class="rvts21">个暗影刺客手下，可以在他桌子上找到一本日记，内容是达利斯复活了布雷克斯。之后，来到复仇女神号，找到塔昆，寻求他的帮助。他会在终章时为我们祈祷。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1514.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">如果之前我们找到了关于塔昆支线</span><span class="rvts20"></span><span class="rvts21">家族宝藏</span><span class="rvts20"></span><span class="rvts21">中的两件武器碎片，可以让他帮我们合成一把双手剑，该双手剑是《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》中最厉害的双手武器，没有之一。但是这把武器只有一点耐久值，并且无法修复。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



